<script>
import $ from "jquery";
import ReviewApi from "../../apis/ReviewApi";
import UserApi from "../../apis/UserApi";
//import "../../assets/css/user.scss";

export default {
  props: {
    params: { type: Object },
    user: { trpe: Object }
  },
  data() {
    return {
      file: "",
      inputNickname: "",
      inputStatus: "",
      temp: "",
      textbox: []
    };
  },
  mounted() {
    $(document).on("click", ".intercom-emoji-picker-emoji", function(e) {
      $(".test-emoji").append($(this).html());
    });

    $(".intercom-composer-popover-input").on("input", function() {
      var query = this.value;
      if (query != "") {
        $(".intercom-emoji-picker-emoji:not([title*='" + query + "'])").hide();
      } else {
        $(".intercom-emoji-picker-emoji").show();
      }
    });
  },
  methods: {
    readURL(input) {
      var fileList = input.target.files;
      this.file = fileList[0];
      console.log(input);
      if (input.target.files && input.target.files[0]) {
        var reader = new FileReader();
        reader.onload = e => {
          $("#imagePreview").css(
            "background-image",
            "url(" + e.target.result + ")"
          );
          $("#imagePreview").hide();
          $("#imagePreview").fadeIn(650);
        };
        reader.readAsDataURL(fileList[0]);
      }
    },
    emojiPicker(e) {
      e.stopPropagation();
      $(".intercom-composer-emoji-popover").toggleClass("active");
    },
    uploadFile() {
      console.log(typeof this.file);
      console.log(typeof "str");

      if (typeof this.file == typeof "str") {
        this.updateUser();
        return;
      }

      let data = new FormData();
      data.append("file", this.file);
      ReviewApi.uploadFile(
        data,
        res => {
          this.user.profileImg = "http://13.209.16.137:8080/upload/" + res.data; //
          this.updateUser();
        },
        error => {
          console.log("update User");
        }
      );
    },
    updateUser() {
      this.textbox[0] = this.inputNickname;
      this.textbox[1] = this.inputStatus;
      console.log(this.textbox[0]);
      console.log(this.textbox[1]);

      this.user.nickname = this.inputNickname;
      this.user.status = this.inputStatus;

      UserApi.updateUser(
        this.user,
        res => {
          console.log("update success");
          alert(".");
          this.$router.push("/user/profile/" + localStorage.getItem("userId"));
        },
        error => {
          console.log("update error");
        }
      );

      return this.textbox;
    },
    niceback: function() {
      var numberOfEntries = window.history.length;
      if (numberOfEntries > 2) {
        this.$router.go(-1);
      } else {
        var fpath = this.PageData.backCrumb.url;
        this.$router.push({
          path: fpath
        });
      }
    },
  }
};
</script>

