<template>
  <div class="clouds">
    <navbar></navbar>
    <div class="containerA">
      <div class="c1">
        <div class="c2">
          <img class="main_logo" src="../../assets/images/main_logo.png" />
        </div>

        <br />
        <br />
        <div class="c3">
          <h1 class="logo_ment">
            <span> ,</span>
            <span>  </span>
            <span>
              <span class="blinking"></span>.
            </span>
          </h1>
        </div>
      </div>

      <div class="c4">
        <div class="input-with-label">
          <input
            v-model="email"
            v-bind:class="{error : error.email, complete:!error.email&&email.length!==0}"
            @keyup.enter="login"
            id="email"
            placeholder=" ."
            type="text"
          />
          <label for="email"></label>
          <div class="error-text" v-if="error.email">{{error.email}}</div>
        </div>
        <div class="input-with-label">
          <input
            v-model="password"
            type="password"
            v-bind:class="{error : error.password, complete:!error.password&&password.length!==0}"
            id="password"
            @keyup.enter="login"
            placeholder=" ."
          />
          <label for="password"></label>
          <div class="error-text" v-if="error.password">{{error.password}}</div>
        </div>

        <button
          style="margin: 1vh 0"
          class="mybtn mybtn--back btn--login"
          v-on:click="login"
          :disabled="!emailCheck||!passwordCheck"
          :class="{disabled : !emailCheck||!passwordCheck}"
        ></button>

        <div style="margin-bottom:15px; margin-top:15px; color:red;">{{msg}}</div>
        <span style="text-align:center; line-height:50px;">SNS </span>
        <div class="sns-login">
          <kakaoLogin :component="component" />
          <!-- <GoogleLogin :component="component" />
          <NaverLogin :component="component" />-->
        </div>
        <div class="add-option">
          <hr />
          <br />
          <div class="wrap">
            <p>
                ?
              <span>
                <router-link v-bind:to="{name:'ChangePassword'}" class="btn--text"> </router-link>
              </span>
            </p>
          </div>
          <div class="wrap">
            <p>
                 ?
              <span>
                <router-link v-bind:to="{name:'Join'}" class="btn--text"> </router-link>
              </span>
            </p>
          </div>
        </div>
      </div>
      <!-- <div id="footer">
        <div id="foot_login">
          <link href="https://fonts.googleapis.com/icon?family=Material+Icons" rel="stylesheet" />
          <p>
            <router-link v-bind:to="{name:'Main'}" class="div2">
              <button class="footer-btn">
                <i class="material-icons" id="foot_1">house</i>
              </button>
            </router-link>
          </p>
          <p>
            <router-link v-bind:to="{name:'Search'}">
              <button class="footer-btn">
                <i class="material-icons" id="foot_2">search</i>
              </button>
            </router-link>
          </p>
          <p>
            <router-link v-bind:to="{name:'ReviewCreate'}">
              <button class="footer-btn">
                <i class="material-icons" id="foot_3">add_circle_outline</i>
              </button>
            </router-link>
          </p>
          <p>
            <router-link v-bind:to="{name:'Login'}">
              <button class="footer-btn">
                <i class="material-icons" id="foot_4">perm_identity</i>
              </button>
            </router-link>
          </p>
        </div>
      </div> -->
    </div>
  </div>
  <!-- </div> -->
</template>
<script>
import "../../assets/css/background.scss";
import "../../assets/css/style.scss";
import "../../assets/css/user.scss";
import "../../assets/css/base.scss";
import PV from "password-validator";
import * as EmailValidator from "email-validator";
import KakaoLogin from "../../components/user/snsLogin/Kakao.vue";
import GoogleLogin from "../../components/user/snsLogin/Google.vue";
import NaverLogin from "../../components/user/snsLogin/Naver.vue";
import UserApi from "../../apis/UserApi";
import MainHeaderVue from '../../components/common/MainHeader.vue';
import Navbar from "../../components/common/Navbarsub.vue";
import $ from "jquery";

export default {
  data: () => {
    return {
      email: "",
      password: "",
      passwordSchema: new PV(),
      error: {
        email: false,
        passowrd: false
      },
      isSubmit: false,
      emailCheck: false,
      passwordCheck: false,
      component: this,
      msg: ""
    };
  },
  components: {
    KakaoLogin,
    Navbar,
    // GoogleLogin,
    // NaverLogin
  },
  created() {
    localStorage.clear();
    this.component = this;
    this.passwordSchema
      .is()
      .min(8)
      .is()
      .max(100)
      .has()
      .digits()
      .has()
      .letters();
  },
  mounted() {
    this.MainInit();
    var tmp = localStorage.getItem("email");
    if (tmp != null) this.email = tmp;
    tmp = localStorage.getItem("password");
    if (tmp != null) this.password = tmp;
    localStorage.clear();
  },
  watch: {
    password: function(v) {
      this.passwordCheckForm();
      // this.checkForm();
    },
    email: function(v) {
      this.emailCheckForm();
      // this.checkForm();
    }
  },
  methods: {
    MainInit() {
      $(".navTrigger").click(function() {
        $(this).toggleClass("active");
        $("#mainListDiv").toggleClass("show_list");
        $("#mainListDiv").fadeIn();
      });
    },
    emailCheckForm() {
      if (this.email.length >= 0 && !EmailValidator.validate(this.email))
        this.error.email = "  .";
      else this.error.email = false;
      let emailCheck = true;
      Object.values(this.error.email).map(v => {
        if (v) emailCheck = false;
      });
      this.emailCheck = emailCheck;
    },
    passwordCheckForm() {
      if (
        this.password.length >= 0 &&
        !this.passwordSchema.validate(this.password)
      )
        this.error.password = ",   8   .";
      else this.error.password = false;
      let passwordCheck = true;
      Object.values(this.error.password).map(v => {
        if (v) passwordCheck = false;
      });
      this.passwordCheck = passwordCheck;
    },
    checkForm() {
      if (this.email.length >= 0 && !EmailValidator.validate(this.email))
        this.error.email = "  .";
      else this.error.email = false;
      if (
        this.password.length >= 0 &&
        !this.passwordSchema.validate(this.password)
      )
        this.error.password = ",   8   .";
      else this.error.password = false;
      let isSubmit = true;
      Object.values(this.error).map(v => {
        if (v) isSubmit = false;
      });
      this.isSubmit = isSubmit;
    },
    login() {
      this.isSubmit = this.emailCheck & this.passwordCheck;
      if (this.isSubmit) {
        let { email, password } = this;
        let data = {
          email,
          password
        };

        UserApi.requestLogin(
          data,
          res => {
            //     
            var loginstatus = res["data"].data;
            console.log(loginstatus);
            // console.log("res:" + res);
            if (loginstatus == "wrong password") {
              this.msg = "   .";
              this.password = "";
            } else if (loginstatus == "true") {
              this.isSubmit = true;
              //login   main 
              console.log(res);
              localStorage.setItem("JWT", res["data"].token);
              localStorage.setItem("userId", res["data"].object.userId);
              localStorage.setItem("nickname", res["data"].object.nickname);
              localStorage.setItem("useremail", res["data"].object.email);
              // console.log(localStorage.getItem("userId"));
              // console.log(localStorage.getItem("JWT"));
              alert(localStorage.getItem("nickname") + " .");
              this.$router.push("/main");
            } else if (loginstatus == "not confirm") {
              this.msg = "  .";
              this.password = "";
            } else if (loginstatus == "no email") {
              this.msg = " .";
              this.password = "";
            }
            if (res == null) {
              //
              this.msg = "  ";
              this.password = "";
            }

            //   
          },
          error => {
            //   
            this.password = "";
            this.email = "";
            this.isSubmit = true;
            //local storage

            localStorage.setItem("email", data.email);
            localStorage.setItem("password", data.password);
            this.$router.push("/Error");
          }
        );
        //   
        // this.emailCheck = false;
        this.passwordCheck = false;
      }
    }
  }
};
</script>