<template>
  <div class="bg-img">
    <div class="overlay">
      <div class="containerB">
        <div class="c1">
          <div class="c2">
            <img class="main_logo" src="../../assets/images/main_logo.png" />
          </div>
          <br />
          <br />
          <div class="c3">
            <h1 class="logo_ment">
              <span> ,</span>
              <span>  </span>
              <span>
                <span class="blinking"></span>.
              </span>
            </h1>
          </div>
        </div>
        <div class="c4">
          <!-- <h1 style="margin: 5vh 0 0; font-size: 30px;"> </h1> -->
          <h3 class="small_ment">
            <p>.</p>
            <p>   :)</p>
          </h3>
          <br />
          <div class="input-with-label">
            <input
              v-model="nickname"
              v-bind:class="{error : error.nickname, complete:!error.nickname&&nickname.length!==0}"
              id="nickname"
              placeholder=" ."
              type="text"
            />
            <label for="nickname"></label>
            <div class="error-text" v-if="error.nickname">{{error.nickname}}</div>
          </div>

          <div class="input-with-label">
            <input
              v-model="email"
              v-bind:class="{error : error.email, complete:!error.email&&email.length!==0}"
              id="email"
              placeholder=" ."
              type="text"
            />
            <label for="email"></label>
            <div class="error-text" v-if="error.email">{{error.email}}</div>
          </div>

          <div class="input-with-label">
            <input
              v-model="password"
              id="password"
              :type="passwordType"
              v-bind:class="{error : error.password, complete:!error.password&&password.length!==0}"
              placeholder=" ."
            />
            <label for="password"></label>
            <div class="error-text" v-if="error.password">{{error.password}}</div>
          </div>

          <div class="input-with-label">
            <input
              v-model="passwordConfirm"
              :type="passwordConfirmType"
              id="password-confirm"
              v-bind:class="{error : error.passwordConfirm, complete:!error.passwordConfirm&&passwordConfirm.length!==0}"
              placeholder="  ."
            />
            <label for="password-confirm"> </label>
            <div class="error-text" v-if="error.passwordConfirm">{{error.passwordConfirm}}</div>
          </div>

          <label>
            <input
              v-model="isTerm"
              type="checkbox"
              id="term"
              v-bind:class="{error : error.term, complete:!error.term}"
            />
            <span> .</span>&nbsp;
          </label>

          <span @click="termPopup" style="cursor:pointer">
            
            <terms-components title=" " :isTerm.sync="isTerm" :visible.sync="visible"></terms-components>
          </span>

          <div
            class="error-text"
            v-if="error.term"
            style="margin-bottom:20px; color:red;"
          >{{error.term}}</div>

          <button
            class="mybtn mybtn--back btn--login"
            v-on:click="join"
            :disabled="!emailCheck||!passwordCheck||!termCheck"
            :class="{disabled : !emailCheck||!passwordCheck||!termCheck}"
          ></button>

          
            <div class="wrap" style="margin-top:30px; font-size: 19px;">
              <router-link
                v-bind:to="{name:'Login'}"
                class="btn--text"
                style="margin: 20px; 15px; float:left;"
              >  </router-link>
            
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/css/user.scss";
import "../../assets/css/layout.scss";
import PV from "password-validator";
import * as EmailValidator from "email-validator";
import UserApi from "../../apis/UserApi";
import JoinTermModal from "./JoinTermModal.vue";

export default {
  data: () => {
    return {
      email: "",
      password: "",
      passwordConfirm: "",
      passwordSchema: new PV(),
      nickname: "",
      isTerm: false,
      isLoading: false,
      error: {
        email: false,
        password: false,
        nickname: false,
        passwordConfirm: false,
        term: false
      },
      isSubmit: false,
      emailCheck: false,
      passwordCheck: false,
      passwordConfirmCheck: false,
      termCheck: false,
      passwordType: "password",
      passwordConfirmType: "password",
      visible: false, //  onoff
      component: this,
      user: {
        email: "",
        password: "",
        nickname: "",
        profileImg: "http://localhost:8080/static/learning.png",
        emailKey: ""
      }
    };
  },
  components: {
    termsComponents: JoinTermModal
  },
  created() {
    this.component = this;
    this.passwordSchema
      .is()
      .min(8)
      .is()
      .max(100)
      .has()
      .digits()
      .has()
      .letters();
  },
  mounted() {
    console.log(localStorage);

    var tmp = localStorage.getItem("nickname");
    if (tmp != null) this.nickname = tmp;
    tmp = localStorage.getItem("email");
    if (tmp != null) this.email = tmp;
    tmp = localStorage.getItem("password");
    if (tmp != null) this.password = tmp;
    tmp = localStorage.getItem("passwordConfirm");
    if (tmp != null) this.passwordConfirm = tmp;

    localStorage.clear();

    //
  },
  watch: {
    password: function(v) {
      this.passwordCheckForm();
    },
    email: function(v) {
      this.emailCheckForm();
    },
    passwordConfirm: function(v) {
      this.passwordCheckForm();
    },
    isTerm: function(v) {
      this.isTermCheckForm();
    }
  },
  methods: {
    emailCheckForm() {
      if (this.email.length >= 0 && !EmailValidator.validate(this.email))
        this.error.email = "  .";
      else this.error.email = false;
      let emailCheck = true;
      Object.values(this.error.email).map(v => {
        if (v) emailCheck = false;
      });
      this.emailCheck = emailCheck;
    },
    passwordCheckForm() {
      if (
        this.password.length >= 0 &&
        !this.passwordSchema.validate(this.password)
      )
        this.error.password = ",   8   .";
      else this.error.password = false;
      if (
        this.passwordConfirm.length >= 0 &&
        this.password != this.passwordConfirm
      ) {
        this.error.passwordConfirm = "  .";
      } else this.error.passwordConfirm = false;
      let passwordCheck = true;
      Object.values(this.error).map(v => {
        if (v) passwordCheck = false;
      });
      this.passwordCheck = passwordCheck;
    },
    passwordConfirmCheckForm() {
      if (
        this.passwordConfirm.length >= 0 &&
        this.password != this.passwordConfirm
      ) {
        this.error.passwordConfirm = "  .";
      } else this.error.passwordConfirm = false;
      let passwordConfirmCheck = true;
      Object.values(this.error).map(v => {
        if (v) passwordConfirmCheck = false;
      });
      this.passwordConfirmCheck = passwordConfirmCheck;
    },
    isTermCheckForm() {
      if (!this.isTerm) {
        this.error.term = "   .";
      } else this.error.term = false;
      let termCheck = true;
      Object.values(this.error.term).map(v => {
        if (v) termCheck = false;
      });
      this.termCheck = termCheck;
    },
    checkForm() {
      if (this.email.length > 0 && !EmailValidator.validate(this.email))
        this.error.email = "  .";
      else this.error.email = false;

      if (
        this.password.length > 0 &&
        !this.passwordSchema.validate(this.password)
      )
        this.error.password = ",   8   .";
      else this.error.password = false;

      if (
        this.passwordConfirm.length > 0 &&
        this.password != this.passwordConfirm
      ) {
        this.error.passwordConfirm = "  .";
      } else this.error.passwordConfirm = false;

      if (!this.isTerm) {
        this.error.term = "   .";
      } else this.error.term = false;

      let isSubmit = true;
      Object.values(this.error).map(v => {
        if (v) isSubmit = false;
      });
      this.isSubmit = isSubmit;
    },
    join() {
      this.isSubmit = this.emailCheck & this.passwordCheck & this.termCheck;
      if (this.isSubmit) {
        this.$set(this.user, "email", this.email);
        this.$set(this.user, "nickname", this.nickname);
        this.$set(this.user, "password", this.password);
        this.$set(
          this.user,
          "profileImg",
          "http://localhost:8080/static/learning.png"
        );
        let { nickname, email, password } = this;

        let data = {
          nickname,
          email,
          password
        };

        //   
        this.isSubmit = false;

        UserApi.requestJoin(
          data,
          res => {
            //     
            console.log("res!!!!");
            console.log(res);
            if (res["data"].data == "already") {
              alert(" .");
              this.email = "";
            } else {
              //   
              this.isSubmit = true;
              this.$router.push({
                name: "EmailAuth",
                params: { user: this.user }
              });
            }
          },
          error => {
            localStorage.setItem("email", this.email);
            localStorage.setItem("password", this.password);
            localStorage.setItem("nickname", this.nickname);
            localStorage.setItem("passwordConfirm", this.passwordConfirm);
            this.$router.push("/error");
          }
        );
      }
    },
    termPopup() {
      this.visible = !this.visible;
    }
  }
};
</script>

<style scoped>
h1,
h3,
a,
span,
label input .logo_ment {
  color: rgb(255, 255, 255);
  font-weight: 400;
}

.input-with-label input[type="text"],
.input-with-label input[type="password"] {
  height: 55px;
  line-height: 1em;
  padding: 0 15px 0 130px;
  color: white;
  border: 1px solid white;
  border-radius: 15px;
}

.input-with-label label {
  position: absolute;
  left: 15px;
  top: 14px;
  color: white;
  font-weight: 400;
  font-size: 1.1em;
  width: auto;
  height: auto;
}

input::placeholder {
  color: rgb(218, 213, 213);
  font-size: 1em;
}

.error-text {
  width: 100%;
  margin-left: 10px;
  float: left;
  color: #ee4b55;
  margin-top: 3px;
  font-weight: 400;
  font-size: 1.1em;
}

.input-with-label input[type="text"].error,
.input-with-label input[type="password"].error {
  border-radius: 0;
  border: none;
  border-bottom: 2px solid #ee4b55;
}

.input-with-label input[type="text"].complete,
.input-with-label input[type="password"].complete {
  border-radius: 0;
  border: none;
  border-bottom: 2px solid #55c5df;
}

[type="checkbox"]:checked + span,
[type="radio"]:checked + span {
  color: #51bfd8;
}

[type="checkbox"]:not(:checked) + span,
[type="radio"]:checked + span,
[type="radio"]:not(:checked) + span {
  position: relative;
  padding-left: 38px;
  cursor: pointer;
  display: inline-block;
  color: rgb(218, 213, 213);
  font-weight: 400;
  line-height: 30px;
}

.mybtn.disabled {
  background: rgba(255, 255, 255, 0.7);
}

.mybtn {
  margin-top: 10px;
  color: black;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "NotoSerifKR";
  margin: 0;
}

.bg-img {
  background: center fixed;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  border: 1px solid;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  animation-name: backgroundmove;
  animation-duration: 15s;
  animation-iteration-count: infinite;
}

@keyframes backgroundmove {
  0% {
    background-image: url("../../assets/images/bg6.jpg");
  }
  25% {
    background-image: url("../../assets/images/bg1.jpg");
  }
  50% {
    background-image: url("../../assets/images/bg3.jpg");
  }
  75% {
    background-image: url("../../assets/images/bg4.jpg");
  }
  100% {
    background-image: url("../../assets/images/bg5.jpg");
  }
}

.overlay {
  background-color: rgba(0, 0, 0, 0.5);
  background-size: cover;
  border: 1px solid;
  height: 100%;
}
</style>
