<template>
  <div id="EmailAuth" class="containerB">
    <div class="c1">
      <div class="c2">
        <img class="main_logo" src="../../assets/images/main_logo.png" />
      </div>
      <br />
      <br />
      <div class="c3">
        <h1 class="logo_ment">
          <span> ,</span>
          <span>  </span>
          <span>
            <span class="blinking"></span>.
          </span>
        </h1>
      </div>
    </div>

    <div class="c4">
      <br />
      <h1
        id="accent"
        v-bind:class="{pulse : accent, animated : accent, pulse2: !accent, animated2 :!accent}"
      >
         .
        <br /> .
      </h1>
      <br />
      <!-- <div class="input-with-label">
        <input
          v-model="tmpemail"
          v-bind:class="{error : error.email, complete:!error.email&&tmpemail.length!==0}"
          id="email"
          placeholder=" ."
          type="text"
        />
        <i style="inline-block;" id="timer" />
        <div class="error-text" v-if="error.email">{{error.email}}</div>
      </div>-->

      <div class="input-with-label">
        <!-- <label style="width: 100%;"> -->
        <div style="width: 70%">
          <input
            v-model="tmpemail"
            v-bind:class="{error : error.email, complete:!error.email&&tmpemail.length!==0}"
            id="email"
            placeholder=" ."
            type="text"
          />
        </div>
        <div style="float: right;">
          <i id="timer">{{resTimeData}}</i>
        </div>
        <!-- </label> -->
      </div>

      <div class="wrap" style="margin-top:40px; margin-bottom:30px;">
        <div class="wrap" style="clear:both;">
          <span style="float:left;">
            <b>  </b>
            <br />  
            <br /> 
          </span>
          <button
            class="btn btn-outline-success"
            @click="EmailVerification"
            :disabled="!isSubmit||!isTimesUp"
            :class="{disabled : !isSubmit||!isTimesUp}"
            style="float:right; display:block; margin-top:13px;"
          > </button>
          <!-- -->
          <!-- <p>{{resTimeData}}</p> -->
          <!-- -->
        </div>
        <h6 style="margin-bottom:30px; display:block; float:right; color:green;">{{msg}}</h6>
        <br />
      </div>

      <button class="btn btn--back btn--login">
        <router-link v-bind:to="{name:'Login'}"> </router-link>
      </button>
    </div>
  </div>
</template>

<script>
import "../../assets/css/layout.scss";
import "../../assets/css/style.scss";
import "../../assets/css/user.scss";
import * as EmailValidator from "email-validator";
import UserApi from "../../apis/UserApi";
import $ from "jquery";

export default {
  props: {
    user: {type: Object}
  },
  data: () => {
    return {
      timerCounter: 10, // 3
      resTimeData: "",
      isTimesUp: false,
      error: {
        email: false
      },
      tmpemail: "",
      isSubmit: false,
      msg: "",
      accent: false,
      originEmail: ""

    };
  },
  created() {
    // 3   
    
    this.startTime();
    this.tmpemail = this.user.email;
    this.originEmail = this.user.email;
    let nickname = this.user.nickname;
    let email = this.user.email;
    let password = this.user.email;
    let data = {
      nickname,
      email,
      password
    };
    UserApi.requestEmailCheck(
      data,
      res => {
        //     
        console.log(res);

        //   
        this.isSubmit = true;
        // this.$router.push("/user/EmailAuth/" + this.email);
      },
      error => {
        // localStorage.setItem("email", this.email);
        // localStorage.setItem("password", this.password);
        // localStorage.setItem("nickname", this.nickname);
        // localStorage.setItem("passwordConfirm", this.passwordConfirm);
        this.$router.push("/error");
      }
    );
  },
  watch: {
    tmpemail: function(v) {
      this.checkForm();
    }
  },
  methods: {
    startTime() {
      // 1   start 
      this.polling = setInterval(() => {
        this.timerCounter--; // 1 
        this.resTimeData = this.prettyTime();
        if (this.timerCounter <= 0){
          this.timeStop();
          this.isTimesUp=true;
        } 
      }, 1000);
    },

    //    
    prettyTime() {
      let time = this.timerCounter / 60;
      let minutes = parseInt(time);
      let seconds = Math.round((time - minutes) * 60);
      return this.pad(minutes, 2) + ":" + this.pad(seconds, 2);
    },

    // 2   09, 08 
    pad(n, width) {
      n = n + "";
      return n.length >= width
        ? n
        : new Array(width - n.length + 1).join("0") + n;
    },

    timeStop() {
      clearInterval(this.polling);
    },

    // 
    emailReset() {
      // alert(this.isSubmit);
      // clearInterval(this.polling);
      // this.timerCounter = 10;
      // this.startTime();
      // this.isTimesUp=false;
      let originEmail = this.originEmail;
      let data = { originEmail };
      UserApi.deleteUser(data,res=>{
        console.log(res);
      },error=>{});


      let nickname = this.user.nickname;
      let email = this.tmpemail;
      let password = this.user.password;

      let data2 = {
          nickname,
          email,
          password
      };

        //   
        this.isSubmit = false;

        UserApi.requestJoin(
          data2,
          res => {
            //     
            console.log(res);
            if(res["data"].data=="already"){
              alert(" .");
            }else{
              clearInterval(this.polling);
              this.timerCounter = 120;
              this.startTime();
              this.isTimesUp=false;
              this.accent = !this.accent;

              UserApi.requestEmailCheck(
                data2,
                res => {
                  //     
                  console.log(res);
  
                  //   
                  this.isSubmit = true;
                  // this.$router.push("/user/EmailAuth/" + this.email);
                },
                error => {
                  localStorage.setItem("email", this.email);
                  localStorage.setItem("password", this.password);
                  localStorage.setItem("nickname", this.nickname);
                  localStorage.setItem("passwordConfirm", this.passwordConfirm);
                  this.$router.push("/error");
                }
              );
  
              //   
              this.isSubmit = true;
            }
            // this.$router.push("/user/EmailAuth/" + this.email);
          },
          error => {
            localStorage.setItem("email", this.email);
            localStorage.setItem("password", this.password);
            localStorage.setItem("nickname", this.nickname);
            localStorage.setItem("passwordConfirm", this.passwordConfirm);
            this.$router.push("/error");
          }
        );
        localStorage.clear();
    },

    EmailVerification() {
      // alert(this.isSubmit);
      // this.isSubmit = false;
      this.checkForm();
      // this.accent = !this.accent;
      this.emailReset();

    },

    checkForm() {
      if (
        this.tmpemail.length >= 0 &&
        !EmailValidator.validate(this.tmpemail)
      ) {
        this.error.email = "  .";
        this.isSubmit = false;
      } else {
        this.error.email = false;
        this.isSubmit = true;
      }
    }
  },
  beforeDestroy() {
    clearInterval(this.polling);
  }
};
</script>

<style scoped>
h1 {
  font-family: "NotoSerifKR";
}

.animated {
  animation-duration: 2.5s;
  animation-fill-mode: both;
  animation-iteration-count: 2.5s;
}
.animated2 {
  animation-duration: 2.5s;
  animation-fill-mode: both;
  animation-iteration-count: 2.5s;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.4);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes pulse2 {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
.pulse {
  animation-name: pulse;
  animation-duration: 1s;
}
.pulse2 {
  animation-name: pulse2;
  animation-duration: 1s;
}
</style>