      <div class="wrapC">
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <div class="card">
          <div class="imgBox">
            <div class="bark"></div>
            <img src="../assets/images/bookcover.png" />
          </div>
          <div class="details">
            <img
              @click="main"
              src="../assets/images/main_logo.png"
              alt="logo image"
              style="align: center; cursor: pointer; width: 200px; margin-left:50px; margin-top:-20px;"
            />
            <br />
            <br />
            <!-- <h4 class="color1"> ,</h4>
            <h4 class="color2 margin"></h4>-->

            <p class="first_card_content"> :)</p>
            <p> ,  .</p>
            <p>    ,</p>
            <p>  ?</p>
            <p> ,   </p>
            <p>   !</p>
            <p class="text-right">Draw your books!</p>
            <p class="text-right">Sketchbook</p>
          </div>
        </div>
        <!--    -->
        <div class="console-container">
          <span id="text"></span>
          <div class="console-underscore" id="console">&#95;</div>
        </div>
      </div>
    </div>
  </div>
  <!-- </div> -->
  <!-- </div> -->
</template>
<script>
import "../assets/css/first.scss";
import "../assets/css/background.scss";
import Kakao from '../../kakao'
export default {
  data: () => {
    return {};
  },
  created() {
    Kakao.init('95b5fffd470fee85112efa7e53c29bca');
  },
  mounted() {
    this.consoleText(
      [
        "Sketch what you read.",
        "    .",
        "Life with Books.",
        "    ."
      ],
      "text",
      ["black", "burlywood", "darkslategray", "dimgray", "saddlebrown"]
    );
  },
  methods: {
    consoleText(words, id, colors) {
      if (colors === undefined) colors = ["#fff"];
      var visible = true;
      var con = document.getElementById("console");
      var letterCount = 1;
      var x = 1;
      var waiting = false;
      var target = document.getElementById("text");
      target.setAttribute("style", "color:" + colors[0]);
      window.setInterval(function() {
        if (letterCount === 0 && waiting === false) {
          waiting = true;
          target.innerHTML = words[0].substring(0, letterCount);
          window.setTimeout(function() {
            var usedColor = colors.shift();
            colors.push(usedColor);
            var usedWord = words.shift();
            words.push(usedWord);
            x = 1;
            target.setAttribute("style", "color:" + colors[0]);
            letterCount += x;
            waiting = false;
          }, 1000);
        } else if (letterCount === words[0].length + 1 && waiting === false) {
          waiting = true;
          window.setTimeout(function() {
            x = -1;
            letterCount += x;
            waiting = false;
          }, 1000);
        } else if (waiting === false) {
          target.innerHTML = words[0].substring(0, letterCount);
          letterCount += x;
        }
      }, 120);
      window.setInterval(function() {
        if (visible === true) {
          con.className = "console-underscore hidden";
          visible = false;
        } else {
          con.className = "console-underscore";

          visible = true;
        }
      }, 400);
    },
    main() {
      this.$router.push("/main");
    }
  }
};
</script>